import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import './App.css';

function App() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [daysBetween, setDaysBetween] = useState(0);
  const [daysToAdd, setDaysToAdd] = useState(0);
  const [futureDate, setFutureDate] = useState(new Date());
  const [mode, setMode] = useState('daysBetween');

  useEffect(() => {
    if (mode === 'daysBetween') {
      const oneDay = 24 * 60 * 60 * 1000;
      const days = Math.round(Math.abs((endDate - startDate) / oneDay));
      setDaysBetween(days);
    } else {
      setFutureDate(new Date(startDate.getTime() + daysToAdd * 24 * 60 * 60 * 1000));
    }
  }, [startDate, endDate, daysToAdd, mode]);

  const CustomInput = ({ value, onClick }) => (
    <button className="custom-input" onClick={onClick}>
      {value}
    </button>
  );

  const toggleMode = () => {
    setMode(mode === 'daysBetween' ? 'futureDate' : 'daysBetween');
  };

  const formatDate = (date) => {
    return date.toLocaleDateString('ru-RU', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  };

  return (
    <div className="App">
      <header className="App-header">
        <div className="date-picker-container">
          <DatePicker className="DatePicker" selected={startDate} onChange={date => setStartDate(date)} customInput={<CustomInput />} />
          {mode === 'daysBetween' && <DatePicker className="DatePicker" selected={endDate} onChange={date => setEndDate(date)} customInput={<CustomInput />} />}
        </div>
        {mode === 'daysBetween' ? (
          <p>Дней: {daysBetween}</p>
        ) : (
          <div>
            <input type="number" value={daysToAdd} onChange={e => setDaysToAdd(e.target.value)} />
            <p>Будущая дата: {formatDate(futureDate)}</p>
          </div>
        )}
        <button className='toogle-mode-button' onClick={toggleMode}>
          {mode === 'daysBetween' ? 'Узнать дату' : 'Узнать количество дней'}
        </button>
      </header>
    </div>
  );
}

export default App;
